html, body {
    height: 100%;
}

body {
    font-family: Arial,NSimsun,sans-serif;
    margin: 0;
    padding: 0;
    -webkit-user-select:none;/* and add the CSS class here instead */
    -ms-user-select:none;
    -moz-user-select:none;
         user-select:none;/**禁止选中文字*/
    background-color: rgb(234,234,234);
}

.main{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 100%;
    min-width: 1023px;
    min-height: 670px;
}
#root{
    width: 100%;
    height: 100%;
}

.GitHub {
    position: absolute;
    width: 100%;
    height: 100%;
    right: 0;
    display: flex;
    justify-content: center;
}

.GitHubBody {
    display: none;
    margin-top: 10%;
    text-align: center;
}

.GitHubBody p{
    font-size: 21px;
    background-image:-webkit-linear-gradient(right, rgb(152, 162, 171),#09afee);
    background-image:linear-gradient(to left, rgb(152, 162, 171),#09afee);
    background-image:-webkit-linear-gradient(left, rgb(152, 162, 171),#09afee);
    -webkit-background-clip: text;
    background-size: 200% 100%;
    -webkit-animation: github 4s infinite cubic-bezier(0, 0.01, 0.32, 1.28);
            animation: github 4s infinite cubic-bezier(0, 0.01, 0.32, 1.28);
    font-weight: bold;
    -webkit-text-fill-color: transparent;
}
@-webkit-keyframes github {
    0%  { background-position: 0 0;}
    100% { background-position: -100% 0;}
}
.Container {
    width: 1023px;
    height: 670px;
    background-color: rgb(250, 250, 250);
    z-index: 10;
}

.tabs {
    width: 823px;
    height: 571px;
    display: inline-block;
    vertical-align: top;
    overflow-y: scroll;
    scrollbar-face-color: rgb(225,225,226);
}

.tabs::-webkit-scrollbar {
    width: 7px;
}

.tabs::-webkit-scrollbar-thumb {
    background: rgb(225,225,226);
    border-radius: 50px;
}

@-webkit-keyframes minimize {
    0%   {opacity: 1}
    25%  {
        opacity: 0.75;
        -webkit-transform: scale(0.75);
                transform: scale(0.75);
        margin-top: 25%;
        margin-right: 25%;
    }
    50%  {
        -webkit-transform: scale(0.5);
                transform: scale(0.5);
        margin-top: 50%;
        margin-right: 50%;
    }
    75%  {
        -webkit-transform: scale(0.25);
                transform: scale(0.25);
        margin-top: 75%;
        margin-right: 75%;
    }
    100% {
        margin-top: 80%;
        margin-right: 90%;
    }
}

@keyframes minimize {
    0%   {opacity: 1}
    25%  {
        opacity: 0.75;
        -webkit-transform: scale(0.75);
                transform: scale(0.75);
        margin-top: 25%;
        margin-right: 25%;
    }
    50%  {
        -webkit-transform: scale(0.5);
                transform: scale(0.5);
        margin-top: 50%;
        margin-right: 50%;
    }
    75%  {
        -webkit-transform: scale(0.25);
                transform: scale(0.25);
        margin-top: 75%;
        margin-right: 75%;
    }
    100% {
        margin-top: 80%;
        margin-right: 90%;
    }
}
