html, body {
    height: 100%;
}

body {
    font-family: Arial,NSimsun,sans-serif;
    margin: 0;
    padding: 0;
    -webkit-user-select:none;/* and add the CSS class here instead */
    -ms-user-select:none;
    user-select:none;/**禁止选中文字*/
    background-color: rgb(234,234,234);
}

.main{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 100%;
    min-width: 1023px;
    min-height: 670px;
}
#root{
    width: 100%;
    height: 100%;
}
